/* eslint-disable @typescript-eslint/no-non-null-assertion */
// eslint-disable-next-line @typescript-eslint/no-explicit-any
type DebounceFunction = <T extends (...args: any[]) => any>(fn: T, delay: number) => (...args: Parameters<T>) => void;

const debounce: DebounceFunction = (fn, delay) => {
  let timer: ReturnType<typeof setTimeout> | null = null
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  return function (this: any) {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any, prefer-const, @typescript-eslint/no-this-alias
    let context: any = this
    // eslint-disable-next-line prefer-const, semi, prefer-rest-params, @typescript-eslint/no-explicit-any
    let args: any[] = Array.from(arguments);
    clearTimeout(timer!)
    timer = setTimeout(function () {
      // eslint-disable-next-line semi
      return fn.apply(context, args);
    }, delay) as unknown as ReturnType<typeof setTimeout>
  }
}

const _ResizeObserver: typeof ResizeObserver = window.ResizeObserver
window.ResizeObserver = class ResizeObserver extends _ResizeObserver {
  constructor (callback: ResizeObserverCallback) {
    callback = debounce(callback, 16)
    super(callback)
  }
}

export { debounce }
