import { computed, defineComponent, onMounted, reactive, toRefs, watch } from 'vue';
import { getGoodsList } from '../request/api';
import { InitData } from '../type/goods';
import { debounce } from '../resize-observer-fix';
export default defineComponent({
  setup() {
    // 定义数据
    const data = reactive(new InitData());
    const comList = computed(() => {
      return data.list.slice((data.selectData.page - 1) * data.selectData.pageSize, data.selectData.page * data.selectData.pageSize);
    });
    // 定义函数
    const getGoods = async () => {
      await getGoodsList().then(res => {
        data.list = res.data;
        data.selectData.count = res.data.length;
      });
    };
    const currentChange = page => {
      data.selectData.page = page;
    };
    const sizeChange = pagesize => {
      data.selectData.pageSize = pagesize;
    };
    const onSubmit = () => {
      let arr = [];
      if (data.selectData.title) {
        arr = data.list.filter(val => val.title.indexOf(data.selectData.title) !== -1);
      } else {
        arr = data.list;
      }
      data.selectData.count = arr.length;
      data.list = arr;
    };
    // 使用函数
    onMounted(() => {
      const _ResizeObserver = window.ResizeObserver;
      window.ResizeObserver = class ResizeObserver extends _ResizeObserver {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        constructor(callback) {
          callback = debounce(callback, 16);
          super(callback);
        }
      };
      getGoods();
    });
    watch([() => data.selectData.title], () => {
      if (data.selectData.title === '') {
        getGoods();
      }
    });
    return {
      comList,
      onSubmit,
      sizeChange,
      currentChange,
      ...toRefs(data)
    };
  }
});